'use client'

import Image from 'next/image'
import { motion } from 'framer-motion'

export default function ExperienceContent() {
  return (
    <section id="experience" className="py-16 lg:py-20 max-w-7xl mx-auto px-4 lg:px-8">
      {/* Creative Heading with subtle background */}
      <div className="text-center mb-20 lg:mb-24 relative z-10">
        <div className="inline-block px-8 py-4 bg-gradient-to-r from-emerald-50/80 to-white/70 backdrop-blur-xl rounded-3xl border border-emerald-200/50 shadow-xl mb-6">
          <motion.h2 
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            viewport={{ once: true }}
            className="text-4xl md:text-5xl lg:text-6xl font-black bg-gradient-to-r from-slate-900 via-slate-700 to-slate-900 bg-clip-text text-transparent leading-tight tracking-tight"
          >
            Professional Experience
          </motion.h2>
        </div>
        <motion.p 
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
          viewport={{ once: true }}
          className="text-lg md:text-xl text-slate-600 max-w-3xl mx-auto leading-snug font-medium"
        >
          Building solutions that matter
        </motion.p>
      </div>

      {/* Creative Zigzag Timeline */}
      <div className="relative max-w-5xl mx-auto">
        {/* Zigzag timeline path */}
        <div className="absolute left-12 lg:left-1/2 w-px bg-gradient-to-b from-slate-200/30 to-slate-300/30 h-full -z-10"></div>
        <div className="absolute top-1/2 left-12 lg:left-1/2 w-20 lg:w-32 h-px bg-gradient-to-r from-slate-200/30 to-slate-300/30 -z-10"></div>

        {/* CodTech Experience - Left Side */}
        <motion.div
          initial={{ opacity: 0, x: -100 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="experience-item mb-24 lg:mb-32 relative"
        >
          {/* Timeline Dot */}
          <div className="absolute left-8 lg:left-1/2 w-5 h-5 lg:w-6 lg:h-6 bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-full shadow-lg border-4 border-white shadow-emerald-400/30 z-20 lg:-translate-x-1/2 lg:-translate-y-1/2"></div>
          
          {/* Main Experience Card */}
          <div className="relative bg-white/95 backdrop-blur-xl rounded-3xl p-8 lg:p-12 border border-slate-200/50 shadow-2xl hover:shadow-3xl hover:-translate-y-2 transition-all duration-700 ml-24 lg:ml-0 lg:max-w-4xl mx-auto group border-emerald-200/30">
            
            {/* Company Header - Clean Design */}
            <div className="flex flex-col lg:flex-row lg:items-start lg:justify-between mb-8 pb-8 border-b border-slate-100">
              <div className="lg:flex-1">
                <h3 className="text-3xl lg:text-4xl font-black text-slate-900 mb-4 tracking-tight leading-tight">
                  CodTech IT Solutions Pvt. Ltd
                </h3>
                <div className="inline-flex items-center px-6 py-3 bg-gradient-to-r from-emerald-50 to-slate-50/80 border border-emerald-200/60 rounded-2xl font-bold text-emerald-800 text-lg shadow-lg hover:shadow-xl transition-all backdrop-blur-sm">
                  <div className="w-2 h-2 bg-emerald-500 rounded-full mr-3 shadow-sm"></div>
                  Full Stack Developer Intern
                </div>
              </div>
              
              <div className="date-location mt-6 lg:mt-0 lg:ml-8 flex flex-col sm:flex-row gap-4 text-slate-600 text-sm lg:text-base font-semibold">
                <div className="date px-6 py-3 bg-gradient-to-r from-slate-50 to-white border border-slate-200/60 rounded-2xl shadow-md text-slate-900 font-bold">
                  Aug 2024 ‚Äì Sep 2024
                </div>
                <div className="location flex items-center px-4 py-3 bg-slate-50/50 border border-slate-200/50 rounded-xl backdrop-blur-sm">
                  <div className="w-1.5 h-1.5 bg-slate-400 rounded-full mr-2"></div>
                  Remote
                </div>
              </div>
            </div>

            {/* Clean Content Layout */}
            <div className="grid lg:grid-cols-12 gap-8 lg:gap-12">
              
              {/* Highlights Column */}
              <motion.div 
                initial={{ opacity: 0, x: -30 }}
                whileInView={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.2, duration: 0.6 }}
                viewport={{ once: true }}
                className="lg:col-span-5 space-y-4"
              >
                <h4 className="text-2xl font-bold text-slate-900 mb-6 flex items-center tracking-tight">
                  <div className="w-8 h-8 bg-gradient-to-r from-yellow-400 to-yellow-500 rounded-xl flex items-center justify-center mr-3 shadow-lg -rotate-3">
                    <div className="w-3 h-3 bg-white rounded-full shadow-sm"></div>
                  </div>
                  Highlights
                </h4>
                <div className="space-y-3">
                  {[
                    { icon: 'shield-check', color: 'emerald', text: '+30% system security' },
                    { icon: 'award', color: 'yellow', text: 'Recommendation letter' },
                    { icon: 'clock', color: 'blue', text: '6-week program' }
                  ].map((highlight, idx) => (
                    <motion.div
                      key={idx}
                      initial={{ opacity: 0, y: 20 }}
                      whileInView={{ opacity: 1, y: 0 }}
                      transition={{ delay: 0.3 + idx * 0.1 }}
                      className="highlight flex items-start p-4 pl-10 relative bg-gradient-to-r from-emerald-50/70 to-white/80 border border-emerald-200/40 rounded-2xl shadow-sm hover:shadow-md hover:-translate-x-1 transition-all backdrop-blur-sm group"
                    >
                      <div className="absolute left-4 top-4 w-2 h-8 bg-gradient-to-b from-emerald-400 to-emerald-500 rounded-r"></div>
                      <div className="w-3 h-3 bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-full mt-1.5 -ml-8 shadow-md group-hover:scale-110 transition-transform"></div>
                      <span className="font-semibold text-slate-800 text-base leading-relaxed ml-1">{highlight.text}</span>
                    </motion.div>
                  ))}
                </div>
              </motion.div>

              {/* Achievements Column */}
              <motion.div 
                initial={{ opacity: 0, x: 30 }}
                whileInView={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.3, duration: 0.6 }}
                viewport={{ once: true }}
                className="lg:col-span-7"
              >
                <h4 className="text-2xl font-bold text-slate-900 mb-6 flex items-center tracking-tight">
                  <div className="w-8 h-8 bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-xl flex items-center justify-center mr-3 shadow-lg rotate-3">
                    <div className="w-2.5 h-2.5 bg-white rounded-full shadow-sm"></div>
                  </div>
                  Key Achievements
                </h4>
                <div className="space-y-3 pr-2 lg:pr-4 max-h-64 overflow-y-auto scrollbar-thin scrollbar-thumb-slate-300/50 scrollbar-track-slate-100/50">
                  {[
                    'Comprehensive full-stack development with data security focus',
                    'JWT authentication, password hashing & salting implementation',
                    'End-to-end construction project management web application',
                    'Real-time collaboration features with Socket.IO',
                    'Dynamic billing system & progress tracking dashboard',
                    'Document management for drawings & tender processes',
                    'Cross-functional team collaboration on user-centric solutions'
                  ].map((achievement, idx) => (
                    <motion.div
                      key={idx}
                      initial={{ opacity: 0, x: 20 }}
                      whileInView={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.4 + idx * 0.03 }}
                      className="achievement flex items-start p-4 bg-white/60 border-l-4 border-slate-200/60 hover:border-emerald-300/60 rounded-xl hover:bg-slate-50/80 transition-all group shadow-sm hover:shadow-md backdrop-blur-sm"
                    >
                      <div className="w-2.5 h-2.5 bg-gradient-to-r from-slate-400 to-slate-500 rounded-full mt-4 mr-4 flex-shrink-0 shadow-sm group-hover:bg-emerald-500 group-hover:scale-110 transition-all"></div>
                      <span className="text-base text-slate-800 leading-relaxed font-medium">{achievement}</span>
                    </motion.div>
                  ))}
                </div>
              </motion.div>

              {/* Tech Stack - Full Width */}
              <motion.div 
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.5, duration: 0.6 }}
                viewport={{ once: true }}
                className="lg:col-span-12 pt-10 border-t border-slate-200/40"
              >
                <h4 className="text-2xl font-bold mb-8 flex items-center text-slate-900 tracking-tight">
                  <div className="w-10 h-10 bg-gradient-to-r from-blue-500 to-blue-600 rounded-2xl flex items-center justify-center mr-4 shadow-lg">
                    ‚öôÔ∏è
                  </div>
                  Technologies Used
                </h4>
                <div className="flex flex-wrap gap-3 justify-center lg:justify-start">
                  {['Node.js', 'Express.js', 'EJS', 'MongoDB', 'Socket.IO', 'JWT', 'Cloudinary', 'HTML5', 'CSS3'].map((tech, idx) => (
                    <motion.span
                      key={tech}
                      initial={{ opacity: 0, y: 20, scale: 0.9 }}
                      whileInView={{ opacity: 1, y: 0, scale: 1 }}
                      whileHover={{ scale: 1.08, y: -3 }}
                      transition={{ delay: 0.6 + idx * 0.04, duration: 0.4 }}
                      className="tech-tag px-6 py-3 bg-white/90 border border-slate-200/70 rounded-2xl font-semibold text-slate-800 text-base shadow-lg hover:shadow-xl hover:border-slate-300/80 transition-all backdrop-blur-xl cursor-pointer hover:bg-slate-50"
                    >
                      {tech}
                    </motion.span>
                  ))}
                </div>
              </motion.div>
            </div>
          </div>
        </motion.div>

        {/* Future Opportunity - Right Side */}
        <motion.div
          initial={{ opacity: 0, x: 100 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          viewport={{ once: true }}
          className="experience-item relative lg:mr-24"
        >
          {/* Future Dot */}
          <div className="absolute right-8 lg:right-1/2 w-5 h-5 lg:w-6 lg:h-6 bg-gradient-to-r from-slate-400 to-slate-500 rounded-full shadow-lg border-4 border-white shadow-slate-400/30 z-20 lg:-translate-x-1/2 lg:-translate-y-1/2 animate-pulse"></div>
          
          {/* Future Card */}
          <div className="relative bg-gradient-to-br from-slate-50/95 to-white/90 backdrop-blur-xl rounded-3xl p-12 lg:p-16 border border-slate-200/50 shadow-2xl hover:shadow-3xl hover:-translate-y-2 transition-all duration-700 mr-24 lg:mr-0 lg:max-w-3xl mx-auto">
            <motion.div
              initial={{ scale: 0.6, opacity: 0, rotate: -180 }}
              whileInView={{ scale: 1, opacity: 1, rotate: 0 }}
              transition={{ duration: 0.8, delay: 0.3 }}
              className="text-6xl lg:text-7xl mb-8 mx-auto"
            >
              üöÄ
            </motion.div>
            <h3 className="text-4xl lg:text-5xl font-black text-slate-900 mb-6 tracking-tight leading-tight">
              Next Opportunity
            </h3>
            <p className="text-xl lg:text-2xl text-slate-700 leading-relaxed font-medium max-w-2xl mx-auto text-center">
              Ready to contribute to innovative projects and grow with forward-thinking teams
            </p>
          </div>
        </motion.div>
      </div>
    </section>
  )
}
